<template>
  <div
    v-if="available_dataset.length != 0"
    class="card card-custom card-stretch gutter-b"
  >
    <div class="card-body pt-0 pb-3 mt-15">
      <div class="tab-content">
        <!-- begin:: Domain Special Transformation Table-->
        <div class="card-header border-0 py-5">
          <!-- begin:: Transformation Table-->
          <div class="card-header border-0 py-5">
            <!--begin:: Transformation desc-->
            <h3 class="card-title align-items-start flex-column">
              <span class="card-label font-weight-bolder text-dark h2"
                >{{ this.trans_fullname }}<br
              /></span>
              <p class="mt-3 text-muted font-size-lg">
                <!-- {{ this.trans_desc }} -->
                Double click the transformation name to get more information and
                transformed dataset
              </p>
            </h3>
            <p>
              <vxe-radio-group v-model="selectTab">
                <template v-for="(item, index) in this.available_dataset">
                  <vxe-radio-button
                    v-bind:key="'tab_radio_' + index"
                    :label="'tab' + index"
                    :content="item"
                  ></vxe-radio-button>
                </template>
              </vxe-radio-group>
            </p>
            <!-- end: dataset selectTab-->
            <!-- begin:: -->
            <template
              v-for="(dataset, dataset_index) in this.available_dataset"
            >
              <div
                v-bind:key="'tab_content_' + dataset_index"
                v-show="selectTab === 'tab' + dataset_index"
              >
                <p
                  v-bind:key="'tab_content_download' + dataset_index"
                  class="mt-0 pt-0 font-size-lg"
                >
                  Click
                  <a
                    :href="
                      'https://www.textflint.com/static/Tasks/' +
                      $store.state.taskinfo.name +
                      '/' +
                      dataset +
                      '-' +
                      trans_type +
                      '.zip'
                    "
                    style="color: rgba(0, 143, 251, 0.85)"
                    >here</a
                  >
                  to download all
                  <span class="font-weight-bolder">{{ trans_fullname }}</span>
                  data in
                  {{ dataset }}
                </p>
                <template
                  v-for="(metric, metric_index) in available_metric[
                    dataset_index
                  ]"
                >
                  <div v-bind:key="'metric_' + metric_index">
                    <WidgetTabTable
                      :dataset="dataset"
                      :trans_type="trans_type"
                      :metric="metric"
                    ></WidgetTabTable>
                  </div>
                </template>
              </div>
            </template>
          </div>
          <!-- end:: UT Transformation Table-->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import "xe-utils";
import VXETable from "vxe-table";
import "vxe-table/lib/style.css";
// const WidgetTabTable = () =>
//   import("@/view/content/widgets/list/widget-tab-table.vue");
const WidgetTabTable = (resolve) =>
  require(["@/view/content/widgets/list/widget-tab-table.vue"], resolve);

// import WidgetTabTable from "@/view/content/widgets/list/widget-tab-table.vue";
Vue.use(VXETable);

export default {
  props: ["trans_type", "dataset_list", "metric_list", "available_trans_list"],
  components: {
    WidgetTabTable,
  },
  data() {
    var trans_info_dict = {
      ut: "Universal Transformations",
      domain: "Task Specific Transformations",
      domain_domain: "Combination of task-specific and task-specific",
      domain_ut: "Combination of universal and task-specific",
      ut_ut: "Combination of universal and universal",
      subpopulation: "Subpopulation",
    };
    var trans_desc = "";
    var trans_fullname = "";
    trans_fullname = trans_info_dict[this.trans_type];
    var available_dataset = [];
    var available_metric = [];
    for (var index in this.dataset_list) {
      var dataset = this.dataset_list[index];
      if (this.available_trans_list[index].indexOf(this.trans_type) != -1) {
        available_dataset.push(dataset);
        available_metric.push(this.metric_list[index]);
      }
    }
    return {
      available_dataset: available_dataset,
      available_metric: available_metric,
      selectTab: "tab0",
      trans_desc: trans_desc,
      trans_fullname: trans_fullname,
    };
  },
};
</script>
